@media (min-width: 768px) {


  .footer-top {
    position: relative;

    .no-padding-xs{
      padding: 0px 15px;
    }

    .background-menu {
      background-color: #3d557d;
      height: 41px;
      position: absolute;
      right: 0px;
      top: 25px;
      left: 45%;
    }

    .container.flex-container {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
      -ms-flex-direction: row;
      flex-direction: row;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;
    }
    .logo-footer {
      float: left;
      // padding: 20px 0px 0px;
      padding: 12px 0px 0px;
      position: relative;
      // top: 0px;
      top: 2px;
      bottom: 0px;
    }


    .footer-menu {


      background-color: transparent;
      position: relative;
      display: inline-block;

      ul {
        padding: 20px 0px 0px;
        margin-left: 75px;
        list-style: none;

        display: flex;
        flex: auto;
        justify-content: center;

        li {
          // padding: 0px;

          &:first-child{
            position: relative;

            &:before{
              content: "";
              background: $primary-color;

              position: absolute;
              top: 5px;
              bottom: 6px;
              left: -50px;

              width: 50px;

              border-top-left-radius: 20px;
              border-bottom-left-radius: 20px;
            }
          }

          a {
            padding: 11.5px 20px;
            background-color: $primary-color;

            @include on-event{
              background-color: #6e81a1;
              text-decoration: none;
            }
          }
        }
      }
    }

  }

  .footer-middle{
    padding: 0px;

    .social-icons {
      float: right;
      margin-bottom: 5px;

    }
  }

  .footer-bottom .copy-list {
    ul {
      li {


        color: #fff;
        margin: 20px 0px;
        display: inline;
        padding: 10px 35px;

        &:last-child{
          display: block;
        }

        &:nth-child(2){
          border-left: 1px solid $white;
        }
        span {
          display: inline;
        }
      }
    }

  }

}

@media (min-width: 992px) {
  .footer-middle{
    // padding: 40px 0px;
    .social-icons {
      float: right;

    }
  }

  .footer-bottom .copy-list {
    ul {
      text-align: right;
      li {


        &:last-child{
          display: inline;
          border-left: 1px solid $white;
          padding-right: 0;
        }


      }
    }

  }

  .footer-top{
    .background-menu{
      top: 26px;
      left: 50%;
      height: 40px;
    }

    .footer-menu{
      ul {
        margin-left: 131px;

        li{
          &:first-child{
            &:before{
              top: 6px;
              bottom: 6px;

            }
          }
        }
      }
    }


  }


}

@media (min-width: 1200px) {

  .footer-middle{
    // padding: 40px 0px;
    .social-icons {
      float: right;

    }
  }

}
